import React, { useState } from 'react';
import { AppstoreOutlined, MailOutlined, MenuOutlined, SettingOutlined, } from '@ant-design/icons';
import { Drawer, MenuProps } from 'antd';
import { Menu } from 'antd';
import "./styles.css";

const items: MenuProps['items'] = [
    {
        label: 'Home',
        key: 'mail',
        icon: <MailOutlined />,
    },
    {
        label: 'Contact Us',
        key: 'alipay',
    },
    {
        label: 'Contact Us',
        key: 'alipay',
    }
];

const App: React.FC = () => {
    const [current, setCurrent] = useState('mail');
    const [openMenu, setOpenMenu] = useState(false);

    const onClick: MenuProps['onClick'] = (e) => {
        console.log('click ', e);
        setCurrent(e.key);
    };

    return <div style={{ height: '100vh', backgroundColor: 'blueviolet' }}>
        <div className='menuIcon' style={{ backgroundColor: 'darkgoldenrod', height: 60 }}>
            <MenuOutlined style={{
                color: 'white',
                paddingLeft: '12px',
                paddingTop: 12,
                fontSize: 17
            }}
                onClick={() => setOpenMenu(true)}
            />
        </div>
        <Menu style={{ display: 'flex' }}>
            <Menu.Item key='home'>Home</Menu.Item>
            <Menu.Item key='option1'>Option 1</Menu.Item>
            <Menu.Item key='notif' style={{ float: 'right' }}>
                Notifications
            </Menu.Item>
            <Menu.Item key='logout' style={{ float: 'right' }}>
                Logout
            </Menu.Item>
        </Menu>
        <Menu className='headerMenu' style={{ backgroundColor: 'darkgoldenrod', color: 'white', fontSize: 16, display: 'flex' }} onClick={onClick} selectedKeys={[current]} mode="horizontal" items={items} />
        <Drawer onClose={() => setOpenMenu(false)} bodyStyle={{ backgroundColor: 'darkgoldenrod' }} open={openMenu} closable={false} >
            <AppMenu />
        </Drawer>
    </div>
};


function AppMenu() {
    return <Menu style={{ backgroundColor: 'darkgoldenrod', color: 'white', fontSize: 16 }} mode="inline" items={items} />

}

export default App;